import React, { useContext, useEffect, useState } from 'react';
import { NavItem } from 'react-bootstrap';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import { 재고context } from './App';
import './Detail.scss';

// class Detail2 extends React.Component {
//   componentDidMount() {}

//   componentWillUnmount() {}
// }

let box = styled.div`
  padding-top: 20px;
`;

let namebox = styled.h4`
  font-size: 25px;
  color: ${(props) => props.cl};
`;

function Detail(props) {
  let [msgflg, chgflg] = useState(true);
  let [text, chgText] = useState('');

  useEffect(() => {
    let timer = setTimeout(() => {
      chgflg(false);
    }, 2000);
    return () => {
      clearTimeout(timer);
    };
  }, [msgflg]);
  // ↑ []를 쓰면 처음 딱 한번만 하고 동작을 안함

  let { id } = useParams();
  const seleteshoes = props.shoes.find((x) => {
    return x.id == id;
  });
  let history = useHistory();

  return (
    <div className='container'>
      <box>
        <namebox className='red'></namebox>
      </box>
      <input
        onChange={(e) => {
          chgText(e.target.value);
        }}
      />
      <p>{text}</p>
      {msgflg === true ? <Yelloalert /> : null}
      <div className='row'>
        <div className='col-md-6'>
          <img
            src={
              'https://codingapple1.github.io/shop/shoes' +
              (seleteshoes.id + 1) +
              '.jpg'
            }
            width='100%'
          />
        </div>
        <div className='col-md-6 mt-4'>
          <h4 className='pt-5'>{seleteshoes.title}</h4>
          <p>상품명 : {seleteshoes.content}</p>
          <p>가격 : {seleteshoes.price} 원</p>
          <Stock 재고={props.재고} />
          <button
            className='btn btn-danger'
            onClick={() => {
              props.재고변경([9, 10, 11]);
            }}
          >
            주문하기
          </button>
          &nbsp;
          <button
            className='btn btn-danger'
            onClick={() => {
              history.goBack();

              // history.push("/"); 페이지 이동시
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );

  function Yelloalert() {
    return (
      <div className='my-alert2'>
        <p>재고가 얼마 남지 않았습니다.</p>
      </div>
    );
  }

  function Stock(props) {
    let test재고 = useContext(재고context);
    return (
      <div>
        <p>재고량 : {props.재고[0]}</p>
        <p>useContext 재고 : {test재고}</p>
      </div>
    );
  }
}

export default Detail;
